import { mountSDK } from '@/loader/loader.js'
import { setBaseUrl, setToken, setRequestHeaders } from '@/utils/http.js';
import store from '@/store/index.js';
import { getRoomInfo } from '@/entity/roomInfo.js'

import requestApi from './request';
import { InteractiveModule, PlayerModule, DocModule, ChatModule } from '@/module/index.js'

let loadAssetsSuccess = false;

const initLoader = function () {
    Promise.all([
        mountSDK('https://static.vhallyun.com/jssdk/vhall-jssdk-player/latest/vhall-jssdk-player-2.3.8.js'),
        mountSDK('https://static.vhallyun.com/jssdk/vhall-jssdk-chat/latest/vhall-jssdk-chat-2.1.3.js'),
        mountSDK('https://static.vhallyun.com/jssdk/vhall-jssdk-interaction/latest/vhall-jssdk-interaction-2.3.3.js')
    ]).then(res => {
        loadAssetsSuccess = true;
    })
}

class VhallSaasSDK {
    constructor() {
        this.msgBus = null;
        this.request = requestApi;
        this.baseState = store;
    }

    init(options = { clientType: 'send', receiveType: 'standard' }) {
        this.setRequestConfig(options)
        this.setClientType(options.clientType)
        if (options.clientType === 'send') {
            return this.initSendLive(options)
        } else {
            return this.initReceiveLive(options)
        }
    }

    initSendLive(options) {
        return new Promise((resolve, reject) => {
            this.request.live.initSendLive(options)
            .then(res => {
                if (res.code === 200) {
                    store.set('roomInitData', getRoomInfo(res))
                    resolve(res)
                } else {
                    reject(res)
                }
            })
        })
    }

    initReceiveLive(options) {
        const receiveApi = {
            standard: 'initStandardReceiveLive',
            embed: 'initEmbeddedReceiveLive',
            sdk: 'initSdkReceiveLive'
        }

        return new Promise((resolve, reject) => {
            this.request.live[receiveApi[options.receiveType]](options)
            .then(res => {
                if (res.code === 200) {
                    store.set('roomInitData', getRoomInfo(res))
                    resolve(res)
                } else {
                    reject(res)
                }
            })
        })
    }

    setClientType(clientType) {
        if (clientType !== 'send' && clientType !== 'receive') {
            throw new TypeError('clientType is invalid')
        }
        store.set('clientType', clientType)
    }

    setRequestConfig(options) {
        if (options.development) {
            setBaseUrl('https://t-saas-dispatch.vhall.com')
        } else {
            setBaseUrl('https://saas-api.vhall.com')
        }

        setToken(options.token, options.liveToken)

        if (options.requestHeaders) {
            setRequestHeaders(options.requestHeaders)
        }
    }

    isReady() {
        return loadSuccess === true;
    }

    createPlayer(options = {}) {
        return new Promise((resolve, reject) => {
            const instance = new PlayerModule(options)
            resolve(instance)
        })
    }

    createInteractive(options = {}) {
        return new Promise((resolve, reject) => {
            const instance = new InteractiveModule(options)
            instance.init(options).then(res=>{
                resolve(instance)
            })
        })
    }

    createChat(options = {}) {
        return new Promise((resolve, reject) => {
            const instance = new ChatModule()
            instance.init(options).then(res => {
                resolve(instance)
            })
        })
    }

    createDoc(options = {}) {
        return new Promise((resolve, reject) => {
            const instance = new DocModule(options)
            resolve(instance)
        })
    }
}
VhallSaasSDK.requestApi = requestApi

initLoader()


export default VhallSaasSDK